.filter-header{
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
}
input {
  padding: 12px 20px;
  border: none;
  background: #f7f6f6;
  outline: none;
  border-radius: 5px;
  position: relative;
  width: 16rem;
}

.nav-icons {
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 2rem;
}


@media (max-width: 480px) {
  .filter-header{
    width: 100%;
    flex-direction: column;
  }
}
